import { Parallax, ParallaxLayer } from '@react-spring/parallax';
import { useRef } from 'react';
import './App.scss';
import First from './Pages/First';
import zizekmarx from './assets/zizekmarx.jpg'
import ProjectItem from './Components/ProjectItem';
import Project from './Pages/Project';
import freudtinder from './assets/freudtinder.png'
import ts from './assets/ts.svg'
import { useMediaQuery } from 'react-responsive';
function App() {
  const parallaxRef = useRef()
  const stackIcons={
    react: 'https://upload.wikimedia.org/wikipedia/commons/a/a7/React-icon.svg',
    ts:ts,
    firebase: 'https://cdn.worldvectorlogo.com/logos/firebase-1.svg'
  }
  const isMobile = useMediaQuery({ maxWidth: 767 })
  return (
    <div className="App">
      <Parallax 
      pages={2} 
      ref={parallaxRef}
      style={{ top: '0', left: '0' }}
      >
        <ParallaxLayer
          speed={!isMobile?0.3:0}
          style={{ display: 'flex', justifyContent: 'center', alignItems: 'center',backgroundColor: '#fefffe' }}>
          <First/>
        </ParallaxLayer>
        <ParallaxLayer 
        offset={1} 
        speed={!isMobile?0.4:0}
        style={{ backgroundImage: `url('${zizekmarx}')`, backgroundRepeat:'repeat' }}
        className='flex items-center justify-center'
        >     
          <Project>
            <ProjectItem image={freudtinder}
            title='Freud`Tinder'
            description='Приложение для знакомств а-ля Tinder с эстетикой психоаналитики без психоаналитики, это как в том анекдоте:
            - Черный кофе. Без сливок, пожалуйста. - К сожалению сливок нет, как насчет черного кофе без молока?
            '
            features={['Firebase-powered бэкенд', 'Свайпы с помощью Framer-motion', 'Использование UI библиотеки MaterialUI', ]}
            stack={stackIcons}
            githubhref='https://github.com/eva01pilot/freudtinder'
            livehref='https://freudtinder.web.app/'
            />
          </Project>
        </ParallaxLayer>
      </Parallax>
    </div>
  );
}

export default App;
