import { Paper } from '@mui/material'
import React, { useState } from 'react'
import { useMediaQuery } from 'react-responsive'

const ProjectItem = ({image, title, description, githubhref, livehref, features, stack}) => {
    const [showHrefs, setShowHrefs] = useState(false)
    const isMobile = useMediaQuery({ maxWidth: 767 })

  return (
    <Paper elevation={20} className='grid grid-cols-2 grid-rows-1 md:flex md:flex-col'>
        <section  onMouseEnter={()=>setShowHrefs(true)} onMouseLeave={()=>setShowHrefs(false)} className='h-full relative'>
            {(showHrefs || isMobile) && <div className='bg-slate-700 opacity-80 h-full w-full absolute top-0 left-0 z-20 flex items-center justify-center'>
                <a href={githubhref}><button className='z-50 text-white text-2xl p-6 bg-neutral-500 mr-4'>Github</button></a>
                <a href={livehref}><button className='z-50 text-white text-2xl p-6 bg-neutral-500'>Live</button></a>
            </div>}
            <img src={image} className='w-full h-full object-contain md:w-auto md:h-auto' alt=''/>
        </section>
        <section className=' text-center bg-slate-700 text-neutral-200 md:text-xs'>
            <h1 className='md:text-2xl'>{title}</h1>
            <div className='p-12 md:p-6 text-justify'>
                <p>{description}</p>
                <h2 className='md:p-4'>Фичи:</h2>
                <ul>
                    {features.map((feature) => {return(
                        <li key={feature}>{feature}</li>
                    )})}
                </ul>
                <h2  className='md:p-4'>Стэк:</h2>
                <ul className='flex flex-row'>
                    <li><img className=' w-16 h-16 object-contain' src={stack.react}></img></li>
                    <li><img className=' w-16 h-16 object-contain' src={stack.firebase}></img></li>
                    <li><img className=' w-16 h-16 object-contain' src={stack.ts}></img></li>
                </ul>
            </div>
        </section>
    </Paper>
  )
}

export default ProjectItem